import { useEffect, useState, useCallback } from "react"
import { useDispatch } from "react-redux"
import { setIsMobileDevice } from "../store/redux/authReducer"

const useFullscreenOnMobile = () => {
  const [isFullscreen, setIsFullscreen] = useState(false)
  const dispatch = useDispatch()

  const isMobileDevice = useCallback(() => {
    return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)
  }, [])

  const checkFullscreen = useCallback(() => {
    return (
        document.fullscreenElement ||
        document.webkitFullscreenElement ||
        document.mozFullScreenElement ||
        document.msFullscreenElement
    )
  }, [])

  const enableFullscreen = useCallback(() => {
    try {
      if (document.documentElement.requestFullscreen) {
        document.documentElement.requestFullscreen()
      } else if (document.documentElement.mozRequestFullScreen) {
        document.documentElement.mozRequestFullScreen()
      } else if (document.documentElement.webkitRequestFullscreen) {
        document.documentElement.webkitRequestFullscreen()
      } else if (document.documentElement.msRequestFullscreen) {
        document.documentElement.msRequestFullscreen()
      }
    } catch (error) {
      console.error("Error enabling fullscreen:", error)
    }
  }, [])

  const handleFullscreenChange = useCallback(() => {
    setIsFullscreen(!!checkFullscreen())
  }, [checkFullscreen])

  const attemptFullscreen = useCallback(() => {
    if (isMobileDevice() && !checkFullscreen()) {
      enableFullscreen()
    }
  }, [isMobileDevice, checkFullscreen, enableFullscreen])

  useEffect(() => {
    if (isMobileDevice()) {
      attemptFullscreen()

      try {
        dispatch(setIsMobileDevice(true))
      } catch (error) {
        console.error("Error dispatching setIsMobileDevice:", error)
      }

      const fullscreenCheckInterval = setInterval(attemptFullscreen, 1000)

      document.addEventListener("fullscreenchange", handleFullscreenChange)
      document.addEventListener("webkitfullscreenchange", handleFullscreenChange)
      document.addEventListener("mozfullscreenchange", handleFullscreenChange)
      document.addEventListener("MSFullscreenChange", handleFullscreenChange)

      window.addEventListener("orientationchange", () => {
        setTimeout(attemptFullscreen, 300)
      })

      return () => {
        clearInterval(fullscreenCheckInterval)
        document.removeEventListener("fullscreenchange", handleFullscreenChange)
        document.removeEventListener("webkitfullscreenchange", handleFullscreenChange)
        document.removeEventListener("mozfullscreenchange", handleFullscreenChange)
        document.removeEventListener("MSFullscreenChange", handleFullscreenChange)
        window.removeEventListener("orientationchange", attemptFullscreen)
      }
    }
  }, [dispatch, isMobileDevice, attemptFullscreen, handleFullscreenChange])

  return isFullscreen
}

export default useFullscreenOnMobile

