import PropTypes from "prop-types"
import { Dialog, DialogTitle, DialogContent, DialogActions, Typography, Button, Box } from "@mui/material"
import { Bell } from "lucide-react"

const NotificationDialog = ({ open, onClose, notification }) => {
    if (!notification) return null

    return (
        <Dialog
            open={open}
            onClose={onClose}
            maxWidth="sm"
            fullWidth
            PaperProps={{
                style: {
                    backgroundColor: "rgba(26, 27, 30, 0.95)",
                    backdropFilter: "blur(10px)",
                    border: "1px solid rgba(255, 255, 255, 0.1)",
                },
            }}
        >
            <DialogTitle sx={{ color: "white", pb: 1 }}>
                <Box sx={{ display: "flex", alignItems: "center", gap: 2 }}>
                    <Box
                        sx={{
                            width: 40,
                            height: 40,
                            borderRadius: "50%",
                            backgroundColor: "rgba(45, 212, 191, 0.1)",
                            display: "flex",
                            alignItems: "center",
                            justifyContent: "center",
                        }}
                    >
                        <Bell size={20} color="#2DD4BF" />
                    </Box>

                    <Typography variant="h6" component="span">
                        {notification.content?.substring(0, 3) || "Notification"}
                    </Typography>
                </Box>
            </DialogTitle>

            <DialogContent>
                <Typography
                    variant="body1"
                    sx={{
                        color: "rgba(255, 255, 255, 0.9)",
                        mb: 2,
                        whiteSpace: "pre-wrap",
                    }}
                >
                    {notification.content}
                </Typography>

                <Typography
                    variant="caption"
                    sx={{
                        color: "rgba(255, 255, 255, 0.5)",
                        display: "block",
                        textAlign: "right",
                    }}
                >
                    {notification.createdAt}
                </Typography>
            </DialogContent>

            <DialogActions>
                <Button
                    onClick={onClose}
                    sx={{
                        color: "#2DD4BF",
                        "&:hover": {
                            backgroundColor: "rgba(45, 212, 191, 0.1)",
                        },
                    }}
                >
                    Close
                </Button>
            </DialogActions>
        </Dialog>
    )
}

NotificationDialog.propTypes = {
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    notification: PropTypes.shape({
        id: PropTypes.string,
        content: PropTypes.string,
        createdAt: PropTypes.string,
    }),
}

export default NotificationDialog

