import React from "react"
import { Box, Container } from "@mui/material"
import { useNavigate } from "react-router-dom"
import AutoLoginCard from "./components/AutoLoginCard"
import { useSelector } from "react-redux"

const AutoLogin = () => {
    const navigate = useNavigate()
    const phoneNumber = useSelector((state) => state.auth?.user?.phoneNumber ?? "")
    const password=useSelector(state => state.auth?.user?.password??"")
    const lastLoginTime = useSelector((state) => state.auth?.user?.lastLoginTime ?? "now")

    const handleCancel = () => {
        navigate("/login/auto/account-select")
    }

    return (
        <Box
            sx={{
                minHeight: "100vh",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                backgroundColor: "#1A1B1E",
                py: 3,
            }}
        >
            <Container maxWidth="sm">
                <AutoLoginCard phoneNumber={phoneNumber} onCancel={handleCancel} lastLoginTime={lastLoginTime} password={password} />
            </Container>
        </Box>
    )
}

export default AutoLogin

