import React, { useEffect, useState } from "react"
import PropTypes from "prop-types"
import {
    Box,
    Container,
    Typography,
    Card,
    CardContent,
    List,
    ListItem,
    ListItemText,
    Divider,
    IconButton,
} from "@mui/material"
import { Bell, CreditCard, DollarSign, UserPlus, Menu } from "lucide-react"
import Sidebar from "../Dashboard/components/Sidebar"
import notificationService from "../../api/notification"
import NotificationDialog from "./components/NotificationDialog"
import {useSelector} from "react-redux";

const NotificationIcon = ({ type }) => {
    const icons = {
        payment: DollarSign,
        card: CreditCard,
        friend: UserPlus,
    }
    const Icon = icons[type] || Bell

    return (
        <Box
            sx={{
                width: 40,
                height: 40,
                borderRadius: "50%",
                backgroundColor: "rgba(45, 212, 191, 0.1)",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                mr: 2,
            }}
        >
            <Icon size={20} color="#2DD4BF" />
        </Box>
    )
}

NotificationIcon.propTypes = {
    type: PropTypes.oneOf(["payment", "card", "friend", "general"]).isRequired,
}

const NotificationsPage = ({ mobileOpen, handleDrawerToggle, isFullscreen }) => {
    const [notifications, setNotifications] = useState([])
    const [selectedNotification, setSelectedNotification] = useState(null)
    const [dialogOpen, setDialogOpen] = useState(false)
    const userId = "5137654321"
    const service=new notificationService.constructor(useSelector(state => state.auth.user))
    useEffect(() => {
        // Connect to WebSocket when component mounts

        service.connect(userId)

        // Subscribe to notifications
        const unsubscribe = service.subscribe((newNotification) => {
            setNotifications((prev) => [newNotification, ...prev])
        })

        // Cleanup on unmount
        return () => {
            unsubscribe()
            service.disconnect()
        }
    }, [userId])
    useEffect(() => {
        const fetchNotifications = async () => {
            try {
              
                const allNotifications = await service.getNotices()
                setNotifications((prev) => [...allNotifications, ...prev])
            } catch (error) {
                console.error("Failed to fetch notifications:", error)
            }
        }

        fetchNotifications()
    }, [])

    return (
        <Box sx={{ display: "flex" }}>
            <Sidebar mobileOpen={mobileOpen} handleDrawerToggle={handleDrawerToggle} />

            <Box
                component="main"
                sx={{
                    flexGrow: 1,
                    minHeight: "100vh",
                    backgroundColor: "#1A1B1E",
                    ml: { sm: "240px" },
                    width: { sm: `calc(100% - 240px)` },
                    py: 3,
                }}
            >
                <Container maxWidth="xl">
                    <Box sx={{ display: "flex", alignItems: "center", mb: 4 }}>
                        <IconButton
                            color="inherit"
                            aria-label="open drawer"
                            edge="start"
                            onClick={handleDrawerToggle}
                            sx={{ mr: 2, display: { sm: "none" } }}
                        >
                            <Menu />
                        </IconButton>

                        <Typography variant="h4" sx={{ color: "white" }}>
                            Notifications
                        </Typography>
                    </Box>

                    <Card
                        sx={{
                            backgroundColor: "rgba(26, 27, 30, 0.8)",
                            backdropFilter: "blur(10px)",
                            border: "1px solid rgba(255, 255, 255, 0.1)",
                            borderRadius: 2,
                        }}
                    >
                        <CardContent>
                            <List sx={{ py: 0 }}>
                                {notifications.length === 0 ? (
                                    <ListItem>
                                        <Typography sx={{ color: "white" }}>No notifications yet</Typography>
                                    </ListItem>
                                ) : (
                                    notifications.map((notification, index) => (
                                        <React.Fragment key={notification.id}>
                                            <ListItem
                                                sx={{
                                                    py: 2,
                                                    px: 3,
                                                    backgroundColor: notification.read ? "transparent" : "rgba(45, 212, 191, 0.05)",
                                                    "&:hover": {
                                                        backgroundColor: "rgba(45, 212, 191, 0.1)",
                                                        cursor: "pointer",
                                                    },
                                                }}
                                                onClick={() => {
                                                    setSelectedNotification(notification)
                                                    setDialogOpen(true)
                                                }}
                                            >
                                                <NotificationIcon type={notification.type} />

                                                <ListItemText
                                                    primary={
                                                        <Typography
                                                            variant="subtitle1"
                                                            sx={{
                                                                color: "white",
                                                                fontWeight: notification.read ? 400 : 600,
                                                                mb: 0.5,
                                                            }}
                                                        >
                                                            {notification.content.substring(0, 3)}
                                                        </Typography>
                                                    }
                                                    secondary={
                                                        <Box>
                                                            <Typography
                                                                variant="body2"
                                                                sx={{
                                                                    color: "rgba(255, 255, 255, 0.7)",
                                                                    mb: 0.5,
                                                                }}
                                                            >
                                                                {notification.content}
                                                            </Typography>

                                                            <Typography
                                                                variant="caption"
                                                                sx={{
                                                                    color: "rgba(255, 255, 255, 0.5)",
                                                                }}
                                                            >
                                                                {notification.createdAt}
                                                            </Typography>
                                                        </Box>
                                                    }
                                                />
                                            </ListItem>

                                            {index < notifications.length - 1 && (
                                                <Divider sx={{ backgroundColor: "rgba(255, 255, 255, 0.1)" }} />
                                            )}
                                        </React.Fragment>
                                    ))
                                )}
                            </List>
                        </CardContent>
                    </Card>

                    <NotificationDialog
                        open={dialogOpen}
                        onClose={() => setDialogOpen(false)}
                        notification={selectedNotification}
                    />
                </Container>
            </Box>
        </Box>
    )
}

NotificationsPage.propTypes = {
    mobileOpen: PropTypes.bool.isRequired,
    handleDrawerToggle: PropTypes.func.isRequired,
    isFullscreen: PropTypes.bool.isRequired,
}

export default NotificationsPage

