import { useState, useEffect } from "react"
import { Box, Typography, IconButton, Avatar, LinearProgress } from "@mui/material"
import { X } from "lucide-react"
import PropTypes from "prop-types"
import { useNavigate } from "react-router-dom"
import { useDispatch } from "react-redux"
import { login } from "../../../../api/user_account" // 确保路径正确

const AutoLoginCard = ({ phoneNumber, lastLoginTime, onCancel,password }) => {
    const [progress, setProgress] = useState(0)
    const dispatch = useDispatch()
    const navigate = useNavigate()

    useEffect(() => {
       if (!phoneNumber) {
         navigate("/login")
            }
    
        const timer = setInterval(() => {
            setProgress((oldProgress) => {
                if (oldProgress === 100) {
                    clearInterval(timer)
                    handleLogin()
                    return 100
                }

                const diff = Math.random() * 10
                return Math.min(oldProgress + diff, 100)
            })
        }, 500)

        return () => {
            clearInterval(timer)
        }
    }, [])

    const handleLogin = async () => {
        try {
            const response =  await login({
                phoneNumber: phoneNumber,
                password: password,
            });

            if (response.status === 200) {
                // 登录成功
                console.log("Auto login successful")
                // 这里可以dispatch一个action来更新Redux store
                // dispatch(loginSuccess(response.data))
                navigate("/dashboard")
            } else {
                // 登录失败
                console.error("Auto login failed:", response.data)
                navigate("/login")
            }
        } catch (error) {
            console.error("Error during auto login:", error)
            navigate("/login")
        }
    }


    return (
        <Box sx={{ textAlign: "center", maxWidth: 400, mx: "auto" }}>
            <Typography
                variant="h2"
                sx={{
                    color: "#2DD4BF",
                    fontWeight: 700,
                    letterSpacing: "-0.02em",
                    fontSize: { xs: "2.5rem", sm: "3rem", md: "4.5rem" },
                    mb: 6,
                }}
            >
                rife.
            </Typography>

            <Box
                sx={{
                    position: "relative",
                    backgroundColor: "rgba(26, 27, 30, 0.8)",
                    backdropFilter: "blur(10px)",
                    borderRadius: 2,
                    p: 4,
                }}
            >
                <Typography
                    variant="h4"
                    sx={{
                        color: "white",
                        fontWeight: 500,
                        mb: 1,
                    }}
                >
                    We&#39;re signing you in
                </Typography>

                <Typography
                    variant="body1"
                    sx={{
                        color: "rgba(255, 255, 255, 0.7)",
                        mb: 4,
                    }}
                >
                    Stay connected to your financial world
                </Typography>

                <Box
                    sx={{
                        backgroundColor: "rgba(255, 255, 255, 0.05)",
                        borderRadius: 1,
                        p: 2,
                        display: "flex",
                        alignItems: "center",
                        gap: 2,
                        mb: 4,
                        position: "relative",
                    }}
                >
                    <Avatar
                        sx={{
                            bgcolor: "#2DD4BF",
                            width: 48,
                            height: 48,
                        }}
                    >
                        {phoneNumber.slice(-2)}
                    </Avatar>

                    <Box sx={{ textAlign: "left", flex: 1 }}>
                        <Typography sx={{ color: "white", mb: 0.5 }}>{phoneNumber}</Typography>

                        <Typography
                            variant="body2"
                            sx={{
                                color: "rgba(255, 255, 255, 0.7)",
                            }}
                        >
                            {lastLoginTime}
                        </Typography>
                    </Box>

                    <IconButton
                        onClick={onCancel}
                        sx={{
                            position: "absolute",
                            right: -8,
                            top: -8,
                            color: "rgba(255, 255, 255, 0.7)",
                            "&:hover": { backgroundColor: "rgba(255, 255, 255, 0.1)" },
                        }}
                    >
                        <X size={20} />
                    </IconButton>
                </Box>

                <LinearProgress
                    variant="determinate"
                    value={progress}
                    sx={{
                        mb: 3,
                        height: 2,
                        borderRadius: 1,
                        backgroundColor: "rgba(45, 212, 191, 0.1)",
                        "& .MuiLinearProgress-bar": {
                            backgroundColor: "#2DD4BF",
                            transition: "transform 0.4s linear",
                        },
                    }}
                />

                <Box sx={{ display: "flex", alignItems: "center", gap: 1, justifyContent: "center" }}>
                    <Typography
                        variant="body2"
                        sx={{
                            color: "rgba(255, 255, 255, 0.5)",
                        }}
                    >
                        If you remain on this page, you&#39;ll be signed in.
                    </Typography>

                </Box>
            </Box>
        </Box>
    )
}

AutoLoginCard.propTypes = {
    phoneNumber: PropTypes.string.isRequired,
    password:PropTypes.string.isRequired,
    onCancel: PropTypes.func.isRequired,
    lastLoginTime: PropTypes.string.isRequired,
}

export default AutoLoginCard

