import SockJS from "sockjs-client"
import { Client } from "@stomp/stompjs"
import axios from "axios";
import {useSelector} from "react-redux";

class NotificationService {
    constructor(User) {
        this.user=User
        this.url=null
        this.rest_call = axios.create({
            baseURL:"http://localhost:8200/api/notifications/",
            validateStatus: (status) => true,
        });
        this.client = null
        this.subscribers = new Set()
        this.connected = false
    }

    connect(userId) {
        // if (this.isConnected()) {
        //     return
        // }

        const socket = new SockJS("http://localhost:8200/ws")

        this.client = new Client({
            webSocketFactory: () => socket,
            debug: (str) => {
                console.log("STOMP Debug:", str)
            },
            onConnect: () => {
                console.log("STOMP connection established")
                this.connected = true

                // Subscribe to personal notifications
                this.client.subscribe(`/user/${userId}/queue/notifications`, (message) => {
                    this.handleNotification(message)
                })

                // Subscribe to topic notifications
                this.client.subscribe(`/topic/notification.${userId}`, (message) => {
                    this.handleNotification(message)
                })
            },
            onStompError: (frame) => {
                console.error("STOMP error:", frame.headers["message"])
                console.error("Additional details:", frame.body)
                this.connected = false
            },
            onDisconnect: () => {
                console.log("STOMP disconnected")
                this.connected = false
            },
        })

        this.client.activate()
    }

    disconnect() {
        if (this.client) {
            this.client.deactivate()
            this.client = null
            this.connected = false
        }
    }

    /**
     *handle recieved message
     * @param message
     */
    handleNotification(message) {
        try {
            const notification = JSON.parse(message.body)
        console.log("recieved message :",notification);
            // Format the notification
            const formattedNotification = {
                id: notification.id || Date.now(),
                title: notification.title || "New Notification",
                message: notification.content || notification.message,
                time: notification.timestamp || "just now",
                type: notification.type || "general",
                isRead: false,
            }

            // Notify all subscribers
            this.subscribers.forEach((callback) => {
                callback(formattedNotification)
            })
        } catch (error) {
            console.error("Error handling notification:", error)
        }
    }

    subscribe(callback) {
        this.subscribers.add(callback)

        return () => {};
    }

    isConnected() {
        return this.connected
    }
    async getNotices() {
        try {
            const response = await this.rest_call.get(this.user.phoneNumber);

            if (response.status !== 200) {
                throw new Error(`HTTP error, status: ${response.status}`);
            }

            return response.data;
        } catch (error) {
            console.error("Error fetching notifications:", error);
            return [];
        }
    }


}

// Create a singleton instance
const notificationService = new NotificationService()
export default notificationService

