import { useState, useEffect } from "react"
import PropTypes from "prop-types"
import {
    Box,
    Typography,
    IconButton,
    Avatar,
    Button,
    Menu,
    MenuItem,
    ListItemIcon,
    ListItemText,
    Divider,
    Badge,
    Popover,
    List,
    ListItem,
} from "@mui/material"
import { WhatsApp } from "@mui/icons-material"
import { Bell, Settings, MessageCircle, HelpCircle, LogOut, MenuIcon, InboxIcon } from "lucide-react"
import { useNavigate } from "react-router-dom"

// Mock notifications data
const mockNotifications = [
    {
        id: 1,
        title: "Payment Received",
        content: "Payment received from John Doe - $50.00",
        createdAt: "2 minutes ago",
        isRead: false,
        type: "payment",
    },
    {
        id: 2,
        title: "Card Added",
        content: "Your new card ending in **** 1234 has been added successfully",
        createdAt: "5 minutes ago",
        isRead: false,
        type: "card",
    },
    {
        id: 3,
        title: "Money Request",
        content: "Jane Smith has requested $25.00 for 'Lunch'",
        createdAt: "10 minutes ago",
        isRead: true,
        type: "payment",
    },
]

// Mock function to simulate new notifications
const mockNewNotification = {
    id: 4,
    title: "New Request",
    content: "New payment request received from Alex Wilson - $30.00",
    createdAt: "Just now",
    isRead: false,
    type: "payment",
}

export default function Header({ handleDrawerToggle }) {
    const navigate = useNavigate()
    const [anchorEl, setAnchorEl] = useState(null)
    const [notificationAnchorEl, setNotificationAnchorEl] = useState(null)
    const [notifications, setNotifications] = useState(mockNotifications)
    const [showAlert, setShowAlert] = useState(false)
    const [currentAlert, setCurrentAlert] = useState(null)
    const open = Boolean(anchorEl)
    const notificationOpen = Boolean(notificationAnchorEl)

    // Simulate receiving new notifications
    useEffect(() => {
        const timer = setInterval(() => {
            const newNotification = {
                ...mockNewNotification,
                id: Date.now(),
                createdAt: "Just now",
            }
            setNotifications((prev) => [newNotification, ...prev])
            setCurrentAlert(newNotification)
            setShowAlert(true)

            // Hide alert after 7 seconds
            setTimeout(() => {
                setShowAlert(false)
            }, 7000)
        }, 15000) // New notification every 15 seconds

        return () => clearInterval(timer)
    }, [])

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget)
    }

    const handleClose = () => {
        setAnchorEl(null)
    }

    const handleNotificationClick = (event) => {
        setNotificationAnchorEl(event.currentTarget)
    }

    const handleNotificationClose = () => {
        setNotificationAnchorEl(null)
    }

    const handleAlertClick = (notification) => {
        setShowAlert(false)
        navigate("/dashboard/notifications")
        // Here you would typically open the notification dialog
    }

    const unreadCount = notifications.filter((n) => !n.isRead).length

    const handleNotificationItemClick = (notification) => {
        setNotifications(notifications.map((n) => (n.id === notification.id ? { ...n, isRead: true } : n)))
    }

    return (
        <Box
            sx={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                mb: { xs: 2, sm: 4 },
                pt: { xs: 1, sm: 0 }, // Add minimal top padding on mobile
                minHeight: { xs: "48px", sm: "64px" },
                width: "100%",
                overflow: "hidden",
            }}
        >
            <Box sx={{ display: "flex", alignItems: "center", minWidth: 0 }}>
                <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    edge="start"
                    onClick={handleDrawerToggle}
                    sx={{ mr: { xs: 1, sm: 2 }, display: { sm: "none" }, padding: { xs: 0.5, sm: 1.5 } }} // Reduced from padding: 1
                >
                    <MenuIcon size={20} />
                </IconButton>

                <Typography
                    variant="h4"
                    sx={{
                        color: "#2DD4BF",
                        fontWeight: 700,
                        letterSpacing: "-0.02em",
                        fontSize: { xs: "1.5rem", sm: "2rem" },
                        display: showAlert ? { xs: "none", sm: "block" } : "block",
                    }}
                >
                    Dashboard
                </Typography>

                {showAlert && currentAlert && (
                    <Box
                        sx={{
                            ml: { xs: 1, sm: 4 },
                            display: "flex",
                            alignItems: "center",
                            gap: { xs: 1, sm: 2 },
                            animation: "fadeIn 0.5s ease-in-out",
                            "@keyframes fadeIn": {
                                "0%": { opacity: 0 },
                                "100%": { opacity: 1 },
                            },
                            flex: 1,
                            minWidth: 0,
                        }}
                    >
                        <Bell size={14} color="#2DD4BF" />

                        <Typography
                            variant="body2"
                            sx={{
                                color: "rgba(255, 255, 255, 0.9)",
                                fontSize: { xs: "0.8rem", sm: "0.875rem" },
                                maxWidth: {
                                    xs: "calc(100vw - 200px)",
                                    sm: "300px",
                                    md: "400px",
                                },
                                overflow: "hidden",
                                textOverflow: "ellipsis",
                                whiteSpace: "nowrap",
                            }}
                        >
                            {currentAlert.content}
                        </Typography>
                    </Box>
                )}
            </Box>

            <Box sx={{ display: "flex", alignItems: "center", gap: { xs: 1, sm: 3 }, ml: 2 }}>
                <Button
                    variant="contained"
                    startIcon={<WhatsApp style={{ color: "green" }} />}
                    sx={{
                        backgroundColor: "#2DD4BF",
                        "&:hover": {
                            backgroundColor: "#26b5a3",
                        },
                        textTransform: "none",
                        px: { xs: 2, sm: 3 },
                        display: { xs: "none", md: "flex" },
                        fontSize: { xs: "0.8rem", sm: "0.875rem" },
                    }}
                    href="whatsapp://send?text=Discover Rife, an amazing way to make payments! https://rifeapp.github.io/io/ "
                >
                    Share via WhatsApp
                </Button>

                <IconButton
                    onClick={handleNotificationClick}
                    sx={{
                        position: "relative",
                        backgroundColor: "rgba(255, 255, 255, 0.05)",
                        "&:hover": { backgroundColor: "rgba(255, 255, 255, 0.1)" },
                        width: { xs: 40, sm: 50 },
                        height: { xs: 40, sm: 50 },
                        borderRadius: "50%",
                    }}
                >
                    <Badge
                        badgeContent={unreadCount}
                        color="error"
                        sx={{
                            "& .MuiBadge-badge": {
                                backgroundColor: "#ef4444",
                                color: "white",
                                fontSize: "0.75rem",
                                width: { xs: 16, sm: 20 },
                                height: { xs: 16, sm: 20 },
                                borderRadius: "50%",
                            },
                        }}
                    >
                        <InboxIcon sx={{ color: "white", fontSize: { xs: 20, sm: 24 } }} />
                    </Badge>
                </IconButton>

                <Avatar
                    onClick={handleClick}
                    aria-controls={open ? "account-menu" : undefined}
                    aria-haspopup="true"
                    aria-expanded={open ? "true" : undefined}
                    sx={{
                        bgcolor: "#2DD4BF",
                        width: { xs: 28, sm: 32 },
                        height: { xs: 28, sm: 32 },
                        fontSize: { xs: "0.75rem", sm: "0.875rem" },
                        cursor: "pointer",
                        "&:hover": {
                            opacity: 0.8,
                        },
                    }}
                >
                    JD
                </Avatar>

                <Menu
                    id="account-menu"
                    anchorEl={anchorEl}
                    open={open}
                    onClose={handleClose}
                    onClick={handleClose}
                    PaperProps={{
                        elevation: 0,
                        sx: {
                            overflow: "visible",
                            filter: "drop-shadow(0px 2px 8px rgba(0,0,0,0.32))",
                            mt: 1.5,
                            backgroundColor: "rgba(26, 27, 30, 0.95)",
                            backdropFilter: "blur(10px)",
                            border: "1px solid rgba(255, 255, 255, 0.1)",
                            borderRadius: 2,
                            minWidth: 200,
                            "& .MuiMenuItem-root": {
                                py: 1.5,
                                px: 2,
                                color: "white",
                                "&:hover": {
                                    backgroundColor: "rgba(45, 212, 191, 0.1)",
                                },
                            },
                        },
                    }}
                    transformOrigin={{ horizontal: "right", vertical: "top" }}
                    anchorOrigin={{ horizontal: "right", vertical: "bottom" }}
                >
                    <MenuItem onClick={() => navigate("/dashboard/settings")}>
                        <ListItemIcon>
                            <Settings size={18} color="#2DD4BF" />
                        </ListItemIcon>

                        <ListItemText>Settings</ListItemText>
                    </MenuItem>

                    <MenuItem onClick={() => navigate("/dashboard/support")}>
                        <ListItemIcon>
                            <MessageCircle size={18} color="#2DD4BF" />
                        </ListItemIcon>

                        <ListItemText>Support</ListItemText>
                    </MenuItem>

                    <MenuItem onClick={() => navigate("/dashboard/help-center")}>
                        <ListItemIcon>
                            <HelpCircle size={18} color="#2DD4BF" />
                        </ListItemIcon>

                        <ListItemText>Help Center</ListItemText>
                    </MenuItem>

                    <Divider sx={{ borderColor: "rgba(255, 255, 255, 0.1)" }} />

                    <MenuItem onClick={() => navigate("/autologin/account-select")}>
                        <ListItemIcon>
                            <LogOut size={18} color="#ef4444" />
                        </ListItemIcon>

                        <ListItemText sx={{ color: "#ef4444" }}>Sign out</ListItemText>
                    </MenuItem>
                </Menu>
            </Box>

            <Popover
                open={notificationOpen}
                anchorEl={notificationAnchorEl}
                onClose={handleNotificationClose}
                anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "right",
                }}
                transformOrigin={{
                    vertical: "top",
                    horizontal: "right",
                }}
                PaperProps={{
                    sx: {
                        width: { xs: "calc(100vw - 32px)", sm: 240 }, // Reduced from 280
                        maxHeight: "50vh", // Reduced from 60vh
                        backgroundColor: "rgba(26, 27, 30, 0.98)", // Slightly more opaque
                        backdropFilter: "blur(10px)",
                        border: "1px solid rgba(255, 255, 255, 0.1)",
                        borderRadius: 2,
                        mt: 1.5,
                        overflow: "hidden",
                        boxShadow: "0 4px 30px rgba(0, 0, 0, 0.1)",
                    },
                }}
            >
                <Box
                    sx={{
                        display: "flex",
                        flexDirection: "column",
                        height: "50vh", // Reduced from 60vh
                        maxHeight: "50vh", // Reduced from 60vh
                    }}
                >
                    <Box
                        sx={{
                            p: 1.25, // Reduced from 1.5
                            borderBottom: "1px solid rgba(255, 255, 255, 0.1)",
                            backgroundColor: "rgba(22, 23, 26, 0.98)", // Slightly more opaque
                            backdropFilter: "blur(10px)",
                            position: "sticky",
                            top: 0,
                            zIndex: 2,
                        }}
                    >
                        <Typography variant="subtitle1" sx={{ color: "white", fontSize: "0.9rem" }}>
                            Notifications
                        </Typography>
                    </Box>

                    <List
                        sx={{
                            p: 0,
                            overflowY: "auto",
                            flex: 1,
                            "&::-webkit-scrollbar": {
                                width: "4px", // Reduced from 6px
                            },
                            "&::-webkit-scrollbar-track": {
                                background: "transparent",
                            },
                            "&::-webkit-scrollbar-thumb": {
                                background: "rgba(45, 212, 191, 0.3)",
                                borderRadius: "3px",
                                "&:hover": {
                                    background: "rgba(45, 212, 191, 0.5)",
                                },
                            },
                        }}
                    >
                        {notifications.map((notification) => (
                            <ListItem
                                key={notification.id}
                                onClick={() => handleNotificationItemClick(notification)}
                                sx={{
                                    borderBottom: "1px solid rgba(255, 255, 255, 0.1)",
                                    backgroundColor: notification.isRead ? "transparent" : "rgba(45, 212, 191, 0.05)",
                                    transition: "all 0.2s ease-in-out",
                                    "&:hover": {
                                        backgroundColor: "rgba(45, 212, 191, 0.1)",
                                        cursor: "pointer",
                                    },
                                    p: 1.25, // Reduced from 1.5
                                }}
                            >
                                <Box sx={{ width: "100%" }}>
                                    <Typography
                                        variant="subtitle2"
                                        sx={{
                                            color: "white",
                                            mb: 0.25, // Reduced from 0.5
                                            fontWeight: notification.isRead ? 400 : 600,
                                            fontSize: "0.8rem", // Smaller font size
                                        }}
                                    >
                                        {notification.title}
                                    </Typography>

                                    <Typography
                                        variant="body2"
                                        sx={{
                                            color: "rgba(255, 255, 255, 0.7)",
                                            mb: 0.25, // Reduced from 0.5
                                            lineHeight: 1.3, // Reduced from 1.5
                                            fontSize: "0.75rem", // Smaller font size
                                        }}
                                    >
                                        {notification.content}
                                    </Typography>

                                    <Typography
                                        variant="caption"
                                        sx={{
                                            color: "rgba(255, 255, 255, 0.5)",
                                            fontSize: "0.65rem", // Smaller font size
                                        }}
                                    >
                                        {notification.createdAt}
                                    </Typography>
                                </Box>
                            </ListItem>
                        ))}

                        {notifications.length === 0 && (
                            <ListItem>
                                <Typography sx={{ color: "rgba(255, 255, 255, 0.7)" }}>No notifications</Typography>
                            </ListItem>
                        )}
                    </List>

                    <Box
                        sx={{
                            p: 1.25, // Reduced from 1.5
                            borderTop: "1px solid rgba(255, 255, 255, 0.1)",
                            backgroundColor: "rgba(22, 23, 26, 0.98)", // Slightly more opaque
                            backdropFilter: "blur(10px)",
                            position: "sticky",
                            bottom: 0,
                            zIndex: 2,
                        }}
                    >
                        <Button
                            fullWidth
                            size="small" // Added size prop
                            onClick={() => {
                                navigate("/dashboard/notifications")
                                handleNotificationClose()
                            }}
                            sx={{
                                color: "#2DD4BF",
                                fontSize: "0.75rem", // Smaller font size
                                py: 0.5, // Reduced vertical padding
                                "&:hover": {
                                    backgroundColor: "rgba(45, 212, 191, 0.1)",
                                },
                            }}
                        >
                            View All Notifications
                        </Button>
                    </Box>
                </Box>
            </Popover>
        </Box>
    )
}

Header.propTypes = {
    handleDrawerToggle: PropTypes.func.isRequired,
}

