// Action Types
const LOGIN = 'LOGIN';
const LOGOUT = 'LOGOUT';
const IS_MOBILE_DEVICE = 'IsMobileDevice';

// Initial State
const initialState = {
    user: {
        phoneNumber: '',
        password: '',
        lastLoginTime: 'Last changed 3 months ago',
    },
    accounts: [],
    isAuthenticated: false,
    isMobileDevice: false,
};

// Reducer
const authReducer = (state = initialState, action = {}) => {
    console.log('Action received:', action);

    switch (action.type) {
        case LOGIN: {
            // Prevent duplicate accounts
            const accounts = new Set([...state.accounts, action.payload]);

            return {
                ...state,
                user: action.payload,
                isAuthenticated: true,
                accounts: Array.from(accounts)
            };
        }

        case LOGOUT:
            return {
                ...state,
                user: null,
                isAuthenticated: false,
                accounts: []
            };
        case IS_MOBILE_DEVICE:
            return {
                ...state,
                isMobileDevice: action.payload
            };
        default:
            return state;
    }
};

// Action Creators
export const redux_login = (user) => ({
    type: LOGIN,
    payload: user
});

export const redux_logout = () => ({
    type: LOGOUT
});

export const setIsMobileDevice = (isMobile) => ({
    type: IS_MOBILE_DEVICE,
    payload: isMobile
});

export default authReducer;
