import { Box, Typography, Avatar, Button, Container } from "@mui/material"
import { User, ChevronRight } from "lucide-react"
import { useNavigate } from "react-router-dom"
import { useSelector } from "react-redux"

const AccountSelection = () => {
    const navigate = useNavigate()

    const accounts = useSelector((state) => state.auth.accounts) || []

    return (
        <Box
            sx={{
                minHeight: "100vh",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                backgroundColor: "#1A1B1E",
                py: 3,
            }}
        >
            <Container maxWidth="sm">
                <Box sx={{ textAlign: "center", maxWidth: 400, mx: "auto" }}>
                    <Typography
                        variant="h2"
                        sx={{
                            color: "#2DD4BF",
                            fontWeight: 700,
                            letterSpacing: "-0.02em",
                            fontSize: { xs: "2.5rem", sm: "3rem", md: "4.5rem" },
                            mb: 6,
                        }}
                    >
                        rife.
                    </Typography>

                    <Box sx={{ textAlign: "center", mb: 4 }}>
                        <Typography
                            variant="h4"
                            sx={{
                                color: "white",
                                fontWeight: 500,
                                mb: 2,
                            }}
                        >
                            Choose an account
                        </Typography>

                        <Typography
                            sx={{
                                color: "rgba(255, 255, 255, 0.7)",
                                mb: 4,
                            }}
                        >
                            to continue to Rife
                        </Typography>
                    </Box>

                    <Box
                        sx={{
                            backgroundColor: "rgba(26, 27, 30, 0.8)",
                            backdropFilter: "blur(10px)",
                            borderRadius: 2,
                            overflow: "hidden",
                        }}
                    >
                        {accounts && accounts.length > 0 ? (
                            accounts.map((account, index) => (
                                <Box
                                    key={index}
                                    sx={{
                                        p: 2,
                                        display: "flex",
                                        alignItems: "center",
                                        gap: 2,
                                        cursor: "pointer",
                                        borderBottom: index < accounts.length - 1 ? "1px solid rgba(255, 255, 255, 0.1)" : "none",
                                        "&:hover": {
                                            backgroundColor: "rgba(255, 255, 255, 0.05)",
                                        },
                                    }}
                                    onClick={() => navigate("/dashboard")}
                                >
                                    <Avatar sx={{ bgcolor: "#2DD4BF", width: 56, height: 56 }}>{account.phoneNumber.slice(-2)}</Avatar>

                                    <Box sx={{ flex: 1, textAlign: "left" }}>
                                        <Typography sx={{ color: "white", mb: 0.5 }}>{account.phoneNumber}</Typography>

                                        <Typography variant="body2" sx={{ color: "rgba(255, 255, 255, 0.7)" }}>
                                            {account.email}
                                        </Typography>
                                    </Box>

                                    <ChevronRight size={20} color="rgba(255, 255, 255, 0.7)" />
                                </Box>
                            ))
                        ) : (
                            <Typography sx={{ color: "white", p: 2 }}>No accounts found.</Typography>
                        )}

                        <Box
                            sx={{
                                p: 2,
                                display: "flex",
                                alignItems: "center",
                                gap: 2,
                                cursor: "pointer",
                                "&:hover": {
                                    backgroundColor: "rgba(255, 255, 255, 0.05)",
                                },
                            }}
                            onClick={() => navigate("/login")}
                        >
                            <Avatar
                                sx={{
                                    bgcolor: "rgba(255, 255, 255, 0.1)",
                                    width: 56,
                                    height: 56,
                                }}
                            >
                                <User size={24} color="rgba(255, 255, 255, 0.7)" />
                            </Avatar>

                            <Typography sx={{ color: "rgba(255, 255, 255, 0.7)", flex: 1, textAlign: "left" }}>
                                Use another account
                            </Typography>

                            <ChevronRight size={20} color="rgba(255, 255, 255, 0.7)" />
                        </Box>
                    </Box>

                    <Box sx={{ mt: 4, textAlign: "center" }}>
                        <Typography sx={{ color: "rgba(255, 255, 255, 0.7)" }}>
                            To continue, Google will share your name, email address, and profile picture with Rife.
                        </Typography>
                    </Box>

                    <Button
                        fullWidth
                        variant="outlined"
                        sx={{
                            mt: 3,
                            color: "#2DD4BF",
                            borderColor: "#2DD4BF",
                            "&:hover": {
                                borderColor: "#26b5a3",
                                backgroundColor: "rgba(45, 212, 191, 0.1)",
                            },
                        }}
                        onClick={() => navigate("/signup")}
                    >
                        Create account
                    </Button>
                </Box>
            </Container>
        </Box>
    )
}

export default AccountSelection

